.workingGroupContainer {
    color: var(--back-color);
    margin: 20px;
    width: 100%;
}

.memberContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;    
    min-width: 302px;
}

.topMemberChildsContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}

.topMemberBorder {
    width: calc(100% - 300px);
    border-bottom: solid 2px var(--border-color);
}

.verticalBorder {
    border-left: solid 2px var(--border-color);
    height: 20px;
}

.lowMemberChildsContainer {    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.member {
    background-color: var(--main-color);
    border: solid 2px var(--border-color);
    padding: 10px;
    height: 40px;
    width: 200px;
    text-align: center;
}

.lowMember {
    margin-left: 80px;
}

.memberName {
    font-weight: bold;
}

.memberRole {
    font-size: small;
}

.addMember {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.addMemberInput {
    background-color: var(--main-color);
    color: var(--back-color);
    outline: solid 1px var(--border-color);
    font-size: medium;
    text-align: center;
    width: 75%;
}

.addMemberButton {
    position: absolute;
    right: 0;
    bottom: 0;
}