.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 100vw;
  height: 100vh;
  grid-template-areas: "h" "empty" "c";
  grid-template-rows: 60px 5px 1fr;
  grid-template-columns: 1fr;
}

.app-wrapper-content {
  grid-area: c;
  background-color: var(--back-color);
  display: flex;
  justify-content: center;
  padding: 10px;
}