.container {
    display: flex;
    width: 97vw;
    height: calc(100vh - 90px);
}

.container p {
    width: 100%;
    text-align: center;
}

.gridContainer {
    padding-inline: 20px;
    display: flex;
    overflow: scroll;
}

.structureContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 300px;
    border-right: solid 2px var(--border-color);
    text-align: center;
    padding-right: 20px;
}

.structureContainer label {
    margin-top: 20px;
    color: var(--back-color);
    background-color: var(--main-color);
    border: solid 3px var(--border-color);
    width: fit-content;
    padding: 5px;
    user-select: none;
    cursor: pointer;
}

.smtrFileNameContainer {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
}

.smtrFileName {
    display: flex;
    margin: 5px;
}

.smtrFileName p {
    margin-left: 10px;
}

.icon {
    width: 18px;
    height: 18px;
    color: var(--main-color);
}