.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.controlPanel {
    display: flex;
    width: 100%;
    min-height: 30px;
}

.controlPanel .icon {
    margin-right: 10px;
    width: 30px;
    height: 30px;
}

.header {
    margin-top: 5px;
    color: var(--back-color);
    background-color: var(--main-color);
    border: solid 2px var(--border-color);
    display: flex;
}

.header .item {
    padding-block: 10px;
    text-align: center;
    font-weight: 550;
    font-style: italic;
    border-left: dashed 1px var(--border-color);
    display: flex;    
    flex-direction: column;
    justify-content: center;
}

.header :first-child {
    border-left: none
}

.data {
    border: solid 1px var(--border-color);
    display: flex;
    flex-direction: column;
    margin-top: 5px;
    width: fit-content;
}

.data .item {
    display: flex;
    border-top: solid 1px var(--border-color);
    font-style: italic;
}

.data :first-child {
    border-top: none
}

.data .item .field {
    border-left: dashed 1px var(--border-color);
}

.data .item :first-child {
    border-left: none
}

.field {
    white-space: nowrap;
    overflow: hidden;
}

.field p {
    padding-block: 5px;
}

.field input {
    color: var(--main-color);
    height: 1.75rem;
    width: 100%;
}

.field select {
    height: 100%;
}

.active {
    background-color: var(--input-color);
}

.selected {
    background-color: var(--selection-color);
}