.container {
    display: flex;
    padding-top: 10px;
}

.gridContainer {
    padding-inline: 10px;
    display: flex;
    overflow: scroll;
}

.gridContainer > :not(:first-child) {
    margin-left: 20px;
}

.newUpg {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--back-color);
    border: solid 2px var(--border-color);
    width: fit-content;
    height: fit-content;
    padding: 20px 50px;
}

.newUpg .controlPanel {
    display: flex;
}

.newUpg .controlPanel .icon {
    margin-left: 30px;
    width: 30px;
    height: 30px;
}

.newUpg .controlPanel :first-child {
    margin-left: 0;
}

.newUpg input[type=text] {
    margin-block: 10px;
    width: 300px;
    height: 2rem;
    background-color: var(--input-color);
    border: solid 1px var(--border-color);
    color: var(--main-color);
    text-align: center;
}

.newUpg input[type=file] {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    z-index: -1;
}

.newUpg .fileLabel {
    margin-block: 10px;
    cursor: pointer;
    background-color: var(--main-color);
    border: solid 1px var(--border-color);
    color: var(--back-color);
    padding-block: 5px;
    min-width: 300px;
    font-size: large;
    width: fit-content;
    text-align: center;
}

.newUpg select {
    outline: none;
    min-width: 300px;
    text-align: center;
    padding-block: 5px;
}

.unitPriceContainer {
    display: flex;
    width: 97vw;
    height: calc(100vh - 90px);
}

.upgItemContainer {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    text-align: center;
}

.upgItemContainer .expences {
    position: relative;
    height: fit-content;
    margin-block: 20px;
}

.upgItemContainer .expences .status {
    position: absolute;
    top: 0;
    right: 0;
    background-color: var(--main-color);
    color: var(--back-color);
    border: solid 2px var(--border-color);
    width: 240px;
    padding-block: 7.5px;
    font-weight: bold;
    font-style: italic;
}