:root {
  --main-color: #3E5368;
  --back-color: #EFEFEC;
  --selection-color: #B3A154;
  --border-color: #222B35;
  --input-color: #e8e0ca;
}

body {
  margin: 0;
  color: var(--main-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  background-color: var(--back-color);
}

p {
  margin: 0;
}

input {
  margin: 0;
  padding: 0;
  border: 0;
  outline: none;
  font-style: italic;
  font-size: medium;
}

textarea {
  resize: none;
  margin: 0;
  padding: 0;
  border: 0;
  outline: none;
  font-style: italic;
  font-size: large;
}

select {
  color: var(--main-color);
  background-color: var(--input-color);
  font-size: medium;
  outline: none;
  appearance: none;
}