.projectsContainer {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    align-content: flex-start;
}

.gridContainer {
    display: flex;
}

.gridContainer > :not(:first-child) {
    margin-left: 20px;
}

.project {
    width: min(80%, 900px);
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.project .controlPanel {
    display: flex;
    width: 100%;
    justify-content: center;
    min-height: 30px;
}

.project .controlPanel .icon {
    margin-left: 30px;
    width: 30px;
    height: 30px;
}

.project .group {
    display: flex;
    justify-content: space-between;
    height: fit-content;
    margin-top: 20px;
}

.project .group .header {
    background-color: var(--main-color);
    border: solid 2px var(--border-color);
    color: var(--back-color);
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

.project .group .data {
    background-color: var(--back-color);
    border: solid 1px var(--border-color);
    color: var(--main-color);
    width: calc(70% - 20px);
    height: fit-content;
}

.project .group .data input,
.project .group .data textarea,
.project .group .data select {
    background-color: var(--back-color);
    width: calc(100% - 10px);
    color: var(--main-color);
    padding-left: 10px;
}

.project .group .data input {
    height: 45px;
}

.project .group .data textarea {
    height: calc(100vh - 300px);
}

.project .group .data select {
    height: 45px;
    width: 100%;
}

.project .group .data .textarea {
    height: calc(100vh - 500px);
}

/* project by id */

.projContainer {
    color: var(--main-color);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.projUpload {    
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 100px;
}

.projUploadContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: var(--main-color);
    height: 200px;
    width: 350px;
    margin: 20px;
    border: solid 2px var(--border-color);
}

.projUploadFile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 10px;
    width: 200px;
    text-align: center;
}

.projUploadFile input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.projUploadFileLabel {
    cursor: pointer;
    background-color: var(--back-color);
    padding: 5px;
    padding-left: 35px;
    padding-right: 35px;
    border: solid 1px var(--border-color);
    font-size: large;
}

.projUploadSubmit {
    margin: 10px;
}

.projUploadSubmit button {
    color: var(--main-color);
    background-color: var(--back-color);
    border: solid 1px var(--border-color);
    border-radius: 0;
    cursor: pointer;
    font-size: large;
    height: 40px;
    width: 200px;
}

.projInfo {
    text-align: center;
}

.projUpgSelect {
    position: relative;
    margin: 10px;
}

.projUpgSelect select { 
    background-color: var(--back-color);
    border: solid 1px var(--border-color);
    color: var(--main-color);
    height: 30px;
    width: 250px;
    font-size: medium;
    text-align: center;
}