.module {
    padding-block: 7px;
    margin-inline: 10px;
    width: min(160px, 90%);
    border-radius: 10px;
    border: solid 2px;
    border-color: var(--border-color);
    background-color: var(--back-color);
    text-align: center;
    font-size: smaller;
    display: block;
}

.module {
    text-decoration: none;
    color: var(--main-color);
}

.emptyModule {
    padding-block: 17px;
}

.active {
    border-color: var(--selection-color);
}