.container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    overflow-y: scroll;
    overflow-x: hidden;
    width: 97%;
}

.container .title {
    margin-left: 100px;
    margin-top: 20px;
}

.gridHorizontalContainer,
.gridVerticalContainer {
    display: flex;
}

.gridVerticalContainer {
    margin-top: 20px;
    flex-direction: column;
}

.gridHorizontalContainer > :not(:first-child) {
    margin-left: 20px;
}

.gridWrapper {
    width: fit-content;
}