.container {
    display: flex;
    position: relative;
}

.total {
    position: absolute;
    top: 0;
    right: 0;
    background-color: var(--main-color);
    color: var(--back-color);
    border: solid 2px var(--border-color);
    padding-block: 4px;
    width: 150px;
    text-align: center;
}