.container {
    display: flex;
    flex-direction: column;
    background-color: var(--back-color);
    padding-top: 10px;
}

.item, .header {
    border: solid 1px var(--border-color);
    border-top: none;
    color: var(--main-color);
    display: flex;
    flex-direction: row;
    justify-content: center;
    min-height: 27px;
}

.item:first-child {
    border-top: solid 1px var(--border-color);
}

.header {
    border: solid 2px var(--border-color);
    font-weight: bold;
    text-align: center;
    background-color: var(--main-color);
    color: var(--back-color)
}

.header .name {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 500px;
}

.header .name .icon {
    margin-top: 10px;
    color: var(--back-color);
    height: 20px;
    width: 20px;
}

.header .regions {
    display: flex;
    flex-direction: column;
}

.header .regions .base {
    border-left: solid 1px var(--border-color);
    border-bottom: solid 1px var(--border-color);
    min-width: 400px;
    padding-block: 5px;
}

.header .regions .regionsData {
    display: flex;
    flex-direction: row;
}

.header .regions .regionsData .region {
    border-left: solid 1px var(--border-color);
    width: 150px;
    padding-block: 5px;
}

.data {
    margin-top: 5px;
    display: flex;
    flex-direction: column;
}

.item .name {
    width: 485px;
    padding-left: 15px;
    padding-block: 3px;
    position: relative;
}

.item .price {
    border-left: solid 1px var(--border-color);
    width: 150px;
    text-align: center;
    padding-block: 3px;
    position: relative;
}

.item .price input, .item .name input {
    position: absolute;
    background-color: var(--back-color);
    color: var(--main-color);
    outline: none;
    top: 0;
    left: 0;
    width: 94.7%;
    height: 78%;
    text-align: center;
    font-size: medium;
}

.item .name input {
    width: 98.3%;
    text-align: left;
}