.modalMenu {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: solid 2px var(--border-color);
    position: absolute;
    z-index: 99;
    width: 200px;
    background-color: var(--back-color);
    user-select: none;
    color: var(--main-color);
}

.modalMenu span {
    font-weight: bold;
    margin: 5px;
}

.modalMenuOption {
    text-align: center;
    width: 90%;
    border-top: solid 1px var(--border-color);
    padding: 7px 0;
    cursor: pointer;
    font-size: small;
    font-weight: bold;
}

.modalMenuOption:hover {
    background-color: var(--selection-color);
}