.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: solid 2px var(--border-color);
    background-color: var(--back-color);
    height: fit-content;
    padding: 30px 70px;
}

.container p {
    margin-bottom: 20px;
}

.container label {
    margin-top: 3px;
}

.container label input[type=radio] {
    margin-right: 5px;
}